import {row,col,tr,td,imgLogo,css} from './utils'

// functions titles

function title(point) {

    const {tag = "h1",styles} = point.options// –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—è
    // const tag = point.options.tag ?? 'h1'; –∑–Ω–∞—á–µ–Ω–∏–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é
    // const styles = point.options.styles;
   
    return  row(col(`<${tag}>${point.value}</${tag}>`),css(styles))
 }
 
 function intro(point) {

    const {styles, styles1} = point.options // –¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—è
    


     return row(col(`
     <p>${point.value}</p>
     <p>${point.value1}</p>`,css(styles)),css(styles1),"intro")
 
 }
 
 // header
 function header(point) {

    const styles = point.options.styles

    console.log(styles)

     const content = point.value.map(td).join("")


     // item=>td(item) –º–æ–∂–Ω–æ –∑–∞–ø–∏—Å–∞—Ç—å –∫–∞–∫ –ø—Ä–æ—Å—Ç–æ td —Ä–µ—Ñ–µ—Ä–µ–Ω—Å > point.value.map(td)
 
     return  tr(content,"header",css(styles))
 
 }
// persons
 function person(point) {
    const content = point.value.map(item=>td(item))  // —Å–æ–∑–¥–∞–Ω–∏–µ –º–∞—Å—Å–∏–≤–∞


    return   tr(content.join(""),"person")// –≤—Å—Ç–∞–≤–∏–ª–∏  –º–∞—Å—Å–∏–≤ –≤ —Ç–∞–±–ª–∏—Ü—É

}
 
  function image(point) {
    return imgLogo(point.value)
 
 
 }


 export const templates ={
     title,
     intro,
     header,
     person,
     image
 }